*{
  padding:0;
  margin:0;
}

.main_div{
  width:100%;
  height: 100vh;
  background: #6983aa;
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
  width: 25%;
  /* height:500px; */
  background: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
}
h1{
  color:white;
  background:#8566aa ;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);

}
input{
  text-align: center;
  height: 30px;
  top:10px;
  border:none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom:2px solid #8566aa ;
  outline:none;
}
button{
  width:40px;
  border-radius: 50%;
  background: #8566aa;
  color:#fff;
  font-size: 40px;
  outline: none;
  border:none;
  margin-left:10px;
  box-shadow:  1px 3px 6px 2px rgb(0 0 0 / 30%) !important;
}
button:hover{
  background-color:#20bf6b;

}
ol{
  margin-top:30px;
}
ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color:#8566aa;
  text-transform: capitalize;
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.todo_style .circle{
  width:33px;
  height: 35px;
  background-color: #8566aa;
  border-radius: 50%;
  margin:0 10px 0 30px;
  color:aliceblue;
}
.todo_style .circle:hover{
  background-color: crimson;
}
.todo_style .circle:hover .list_style{
  color:crimson;
}
